.box{
    width: 282px;
}
.box .imgbox{
    width: 100%;
    height: 210px;
}
.box .imgbox img{
    width: 100%;
    height: 100%;
}
.box ul{
    width: 100%;
    height: 112px;
    margin-top: 38px;
}
.box ul li:first-child{
    width: 100%;
    height: 28px;
    display: flex;
    align-items: center;
}
.box ul li:first-child h4{
    font-size: 16px;
}
.box ul li:nth-child(2){
    width: 100%;
    height: 31px;
    display: flex;
    align-items: center;
}
.box ul li:nth-child(2) p{
    font-size: 13px;
}
.box ul li:last-child{
    width: 100%;
    height: 53px;
    display: flex;
    align-items: center;
}
.box ul li:last-child h2{
    font-size: 21px;
}