*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
a{
    display: block;
    text-decoration: none;
}
li{
    list-style: none;
}
img{
    display: block;
    border: none;
}
button{
    border: none;
    background: none;
    cursor: pointer;
}
.wrap{
    width: 1200px;
    margin: auto;
    display: flex;
    justify-content: space-between;
}